import React, { Component } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { HubBox } from '../components';
import {
  Welcome,
  MainPage,
  HubAddPage,
  HubAdminPage,
  NotFound,
  OAuth2RedirectHandler
} from 'pages';
import { PrivateRoute } from 'components';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';
import './App.css';
import {REPO_NAME} from '../constants';
import { MdLocalGasStation } from 'react-icons/md';

const DynamicRoute = ({ match, location }) => {
  //location state가 존재하고 허브의 관리자 id와 hub의 user id가 일치한다면
  if (location.state && location.state.hubInfo.adminId === location.state.userInfo.userId) {
    console.log('ok!')
    return (<Route
      path={mkPath(`${match.url}/:id`)}
      component={HubAdminPage} />
    )
  }
  else {
    console.log('false!')
    return <Route component={NotFound} />
  }
}

const mkPath = (url) => {
  return `${REPO_NAME}${url}`;
}


class App extends Component {
  render() {
    return (
      <div className="App">
        <Switch>
          <Route exact path={mkPath("/")} component={Welcome} />
          {/* <PrivateRoute path="/main" 
          isAutenticated = { this.props.isAutenticated }
          component={Main} /> */}
          <Route path={mkPath("/main")} component={MainPage} />
          <Route path={mkPath("/add")} component={HubAddPage} />
          <Route path={mkPath("/hub")} component={DynamicRoute} />
          <Route path={mkPath("/oauth2/redirect")} component={OAuth2RedirectHandler} />
          <Route component={NotFound} />
        </Switch>
      </div>
    );
  }
}

export default withRouter(
  //subscribe redux store
  connect(
    state => ({
      isAuthenticated: state.auth.getIn(['userState', 'isAuthenticated']),
    })
  )(App)
);
